<script>
export default {
    data() {
        return {
            formState: {},
            index: 1,
            listName: ["账号解冻", "登录账号"],
            captchaKey: "",
            captchaImg: "",
        };
    },
    created() {},
    methods: {
        onSubmit() {
            if (!this.formState.username) {
                this.$message.warning("请输入会员账号");
                return;
            }
            if (!this.formState.name) {
                this.$message.warning("请输入真实姓名");
                return;
            }
            let parames = {
                name: this.formState.username,
                realName: this.formState.name,
            };
            this.$http.post(this.$api.accountThaw, parames).then((res) => {
                console.log("图片", res);
                if (res.code == 0) {
                    this.$message.success("账号解冻成功");
                    this.$router.go(-1);
                }
            });
        },
    },
};
</script>

